html {
  height: 100%;
}
body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: 'PT Sans', sans-serif;
    background: rgba(245, 219, 219, .15);
}

.App {
  text-align: center;
  /* height: 100vh; */
  display: flex;
  justify-content: center;
  flex-direction: column;
    align-items: center;

}

.App-Container {
  background: #fff;
  box-shadow: 1px 2px 3px #c3c3c3;
    width: 80%;
    position: relative;
}

.App-Footer {
  text-align: left;
}
.what-is {
      position: absolute;
    top: 9px;
    right: 10px;
    font-size: 10px;
}
.what-is:hover {
  text-decoration: underline;
  cursor: pointer;
}
a {

  color: dodgerblue;
  text-decoration: none;
}
a:visited {
  color: dodgerblue;
  text-decoration: none;
}

ul {
  list-style: none;
    text-align: left;
    padding: 0;
}
li {
padding: 5px 0;
}
ul p {
    border-bottom: 1px solid #c9c9c9;
    padding-bottom: 0.5em;
    margin-bottom: .5em;
    font-weight: 600;
  }
.timer-container {
    padding: 30px;
    position: relative;
}

.progress {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;

}

.inner-progress {
    background: #ca4c4c;
    height: 2px;
}

.timer{
    font-size: 60px;
    font-family: 'Share Tech Mono', monospace;
    display: flex;
    justify-content: center;
}



.tomato-container {
display: flex;
justify-content: center;
flex-direction: column;
}

.tomato {

    font-size: 13px;
    margin: 0px 4px;
}

@-webkit-keyframes blinker {
  20% { opacity: 0; }
}

@keyframes blinker {
  20% { opacity: 0; }
}
.btn {
    white-space: nowrap;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    padding: 0 14px;
    margin: 10px;
    min-width: 50%;
    box-shadow: 0 4px 6px rgba(50,50,93,.11);
    background: #e56767;
    border-radius: 4px;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: .025em;
    color: #FFF;
    text-decoration: none;
    transition: all .15s ease;
    outline: none;
    border: none;

}

.btn:hover {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    box-shadow: 0 7px 14px rgba(50,50,93,.1);
}
.btn:active {
    background-color: #ca4c4c;
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    box-shadow: 0 4px 6px rgba(50,50,93,.11);
}
.btn-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
}
/* .task:not(:last-of-type) {
    border-bottom: 1px solid #c3c4c5;

} */
.task {
    padding: 10px 5px;
    font-size: 18px;
    font-weight: 600;
    text-align: left;
    border-bottom: 1px solid #c3c4c5;
    display: flex;
    max-height: 30px;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
}
.task-text {
        text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.task-delete {
    font-size: 1.5em;
    padding: 10px;
    cursor: pointer;
}
.task-list-container {
    border-top: 1px solid #c3c4c5;
    margin: 0px 60px 20px 60px;
}

.input-container {
    display: flex;
    justify-content: center;
    margin: 15px;
}

.input {
    border: none;
    outline: none;
    box-shadow: 0 0 1px #c3c3c3;
    background: #f6f9fc;
    padding: 5px 20px 8px 13px;
    border-radius: 3px;
    font-size: 18px;
    transition: background-color .5s linear, color .5s linear, opacity .5s linear;
    flex-basis: 40%;
    max-width: 800px;
}
.input:focus {
    background: #f9f9f9;
}

.backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,.5);
}
.explaination-container {
    position: fixed;
    z-index: 500;
    background-color: #fff;
    width: 70%;
    box-shadow: 1px 2px 3px #c3c3c3;
    padding: 16px;
    left: 15%;
    top: 10%;
    box-sizing: border-box;
    -webkit-transform: translateY(-200vh);
            transform: translateY(-200vh);
    transition: all .4s ease-in-out
}
.explaination-container.open {
        -webkit-transform: translateY(0vh);
                transform: translateY(0vh);

}

